@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+Display&display=swap');

.addCommentForm {
  width: 50%;
  display: flex;
  flex-direction: column;
  font-family: 'Noto Serif Display', serif;
  //margin: 10px;

  h3 {
    margin: 0;
  }

  label {
    display: flex;
    flex-direction: column;
    margin: 5px 0px;
    

    input {
      padding: 5px;
    }
  }

  button {
    width: 100px;
    background-color: tan;
    font-family: 'Noto Serif Display', serif;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    padding: 5px;
  }
}
  
